<template>
    <v-card>
        <v-card-text>
            
            <v-layout align-space-around justify-start column fill-height>
                <div class="userInfoFrame item elevation-5">
                    <div class="left-part">
                        <v-layout row >
                            <v-flex xs5 mt4 >
                                <v-img
                                :src="userInfo['image']"
                                height="95px"
                                weight="75px"
                                contain
                                ></v-img>
                            </v-flex>
                            <v-flex xs7>
                                <div>
                                <div class="headline">{{userInfo['realname']}}</div>
                                <div>{{userInfo['nativeplace']}}</div>
                                <div>{{userInfo['nation']}}</div>
                                </div>
                            </v-flex>
                            
                        </v-layout>

                        <v-btn flat right class="warning mt-5 ml-5" @click="toEditProfile">
                            
                        </v-btn>
                        <v-btn flat right class="success mt-5 ml-5" @click="toProfilePdf">
                            pdf
                        </v-btn>
                    </div>
                    <div class="right-part">
                        
                    </div>
                </div>
                <!-- <div class="userInfoFrame item elevation-5">
                    
                </div> -->
            </v-layout>
        </v-card-text>
    </v-card>
</template>

<script>
import profiledata from '@/api/profiledata'
import util from '@/utils'

export default {
    props:['psid','sname','puid','paid'],
    data(){
        return{
            image:null,
            selectedfile:null,
            username:'',
            nativeplace:'',
            due:null,
            show:false,
            inputRules:[
                v => v.length >= 3 || 'Minimum length is 3 characters'
            ],
            genderitems: ['Male', 'Female'],
            genderselect:'',
            politicalitems: ['','','','','','','','','','','','',''],
            nationitems:['','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','',],
            politicalselect:'',
            nationselect:'',
            college:'',
            grade:'',
            majorset:'',
            eduset:'',
            phone:'',
            email:'',
            eduback:'',
            ug_exp:'',
            other_exp:'',
            date:null,
            data: null,
            menu: false
        }
    },
    watch:{
        menu (val) {
        val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
      }
    },
    computed:{
        getPsid:function (){
            return this.psid
        },
        getPuid:function (){
            return this.puid
        },
        getPaid:function (){
            return this.paid
        },
        userInfo:function () {
            console.log("now is Profile STTTTTTORE")
            console.log(this.$store.getters.getUserInfo)
            return this.$store.getters.getUserInfo
        },
    },
    methods:{
        async toEditProfile(){
            util.toRouter('/user/home/editprofile',this)
        },        
        async toProfilePdf(){
            util.toRouter('/user/home/profilePdf',this)
        },
        savedate (date) {
          this.$refs.menu.save(date)
        },
    },
}
</script>
    
<style>
    #fileArea {
        max-width: 0px;
        margin: 15px 0 5px 32px;
        /* border-style:dotted;
        border-color: black */
    }
    .userInfoFrame{
    /* position: relative; */
    /* top: 55px;
    width: 80%;
    height: 252px; */
    background: #FFFFFF;
    box-shadow: 0 0 20px 0 rgba(59,104,184,0.25);
    border-radius: 10px;
    margin: 20px 40px 20px 40px;
    padding: 20px;
    }
    .left-part {
    display: inline-block;
    vertical-align: top;
    width: 323px;
    }
    .right-part {
    display: inline-block;
    vertical-align: top;
    margin-left: 27.6px;
    }
</style>

    


